@import url("https://fonts.googleapis.com/css2?family=Hanken+Grotesk:wght@500;700;800&display=swap");
:root {
  --Lightred: hsl(0, 100%, 67%);
  --Orangeyyellow: hsl(39, 100%, 56%);
  --Greenteal: hsl(166, 100%, 37%);
  --Cobaltblue: hsl(234, 85%, 45%, 0.9);

  --Lightslateblue: hsl(252, 100%, 67%);
  --Lightroyalblue: hsl(241, 81%, 54%);

  --Violetblue: hsla(256, 72%, 46%, 1);
  --Persianblue: hsla(241, 72%, 46%, 0);

  --White: hsl(0, 0%, 100%);
  --Paleblue: hsl(221, 100%, 96%);
  --Lightlavender: hsl(241, 100%, 89%);
  --Darkgrayblue: hsl(224, 30%, 27%);

  --transCobaltblue: hsla(234, 85%, 45%, 0.1);
  --transLightred: hsla(0, 100%, 67%, 0.1);
  --transOrangeyyellow: hsla(39, 100%, 56%, 0.1);
  --transGreenteal: hsla(166, 100%, 37%, 0.1);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Hanken Grotesk";
}

body {
  width: 100%;
  height: 100vh;
  background-color: var(--White);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.card {
  display: flex;
  flex-direction: row;
  width: 550px;
  height: 400px;
  background-color: var(--White);
  box-shadow: 0 0 20px 2px var(--Paleblue);
  border-radius: 20px;
}

.results {
  display: grid;
  text-align: center;
  background-color: var(--Cobaltblue);
  width: 50%;
  padding: 30px 35px;
  border-radius: 20px;
  font-weight: 500;
  background: linear-gradient(var(--Lightslateblue), var(--Lightroyalblue));
  color: var(--Lightlavender);
}

.results h1 {
  display: flex;
  background-color: aqua;
  justify-content: center;
  flex-direction: column;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  transform: translate(35%);
  background: linear-gradient(var(--Violetblue), var(--Persianblue));
  color: var(--White);
}

.results h1 span {
  font-size: 14px;
  color: var(--Lightlavender);
}

.message h3 {
  color: var(--White);
  padding: 14px;
  font-size: 20px;
}

.message {
  font-size: 14px;
}

.summary {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 50%;
  padding: 30px;
}

.summary ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 70%;
}

.summary li {
  display: grid;
  grid-template-columns: auto 60% auto;
  padding: 15px 10px;
  font-size: 13px;
  align-items: center;
  background-color: aquamarine;
  border-radius: 10px;
}

.red {
  color: var(--Lightred);
}

.orange {
  color: var(--Orangeyyellow);
}

.green {
  color: var(--Greenteal);
}
.blue {
  color: var(--Cobaltblue);
}
.summary li span {
  color: var(--Lightlavender);
}

.summary li {
  font-weight: 800;
}
.summary svg {
  margin-right: 8px;
}

.summary li:nth-child(1) {
  background-color: var(--transLightred);
}

.summary li:nth-child(2) {
  background-color: var(--transOrangeyyellow);
}
.summary li:nth-child(3) {
  background-color: var(--transGreenteal);
}
.summary li:nth-child(4) {
  background-color: var(--transCobaltblue);
}

.summary button {
  border: none;
  border-radius: 50px;
  padding: 10px;
  background-color: var(--Darkgrayblue);
  color: var(--White);
}

.summary button:hover {
  background: linear-gradient(var(--Lightroyalblue), var(--Lightslateblue));
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .card {
    display: flex;
    flex-direction: column;
    width: 300px;
    height: 700px;
  }
  .results {
    width: 100%;
    height: 45%;
  }
  .results h1 {
    width: 100px;
    height: 100px;
    transform: translate(65%);
  }
  .summary {
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: space-between;
  }
}
